.modal {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 999;
  display: flex;
  background-color: rgba(0, 0, 0, 0.3);
}

.modal__container {
  position: relative;
  padding: 28px 28px 36px 28px;
  border-radius: 20px;
  border: 1px solid rgba(0, 0, 0, 0.3);
  margin: auto;
  background-color: #fff;
  max-width: 496px;
  min-width: 280px;
}

.modal__close-button {
  background: transparent url(../../images/buttons/close.svg) center no-repeat;
  border: none;
  position: absolute;
  top: 20px;
  right: 28px;
  width: 20px;
  height: 20px;
  padding: 0;
  cursor: pointer;
}

.modal__title {
  font-family: "Inter", sans-serif;
  font-style: normal;
  font-weight: 700;
  line-height: 20px;
  max-width: 358px;
  margin: 0 0 24px 0;
  color: #000;
  text-align: start;
}

.modal__form {
  display: flex;
  flex-direction: column;
  gap: 32px;
}

.modal__buttons {
  display: flex;
  gap: 12px;
}

.modal__add-button {
  height: 36px;
  padding: 8px 16px;
  border-radius: 4px;
  border: none;
  background: #000;
  color: #fff;
  font-family: "Inter", sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 20px;
}

.modal__add-button:disabled {
  background: rgba(0, 0, 0, 0.3);
  cursor: pointer;
}

.modal__add-button:hover {
  background: rgba(0, 0, 0, 0.6);
  cursor: pointer;
}

.modal__switch-button {
  padding: 0;
  border: none;
  background: none;
  color: rgba(0, 0, 0, 0.5);
  font-family: "Inter", sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 20px;
}

.modal__switch-button:hover {
  cursor: pointer;
}

.form {
  display: flex;
  flex-direction: column;
  gap: 20px;
  margin: 0;
  color: #000;
  font-family: "Inter", sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 20px;
}

.form__title {
  margin: 0 0 8px 0;
  text-wrap: nowrap;
}

.form__title-error {
  color: #ff4d00;
}

.form__field {
  display: flex;
  gap: 10px;
  justify-content: space-between;
}

.form__input {
  padding: 4px 0;
  border: none;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  width: 100%;
}

.form__select-characters {
  width: 400px;
}

.form__input::placeholder {
  color: rgba(0, 0, 0, 0.5);
}

.form__input-error {
  color: #ff4d00;
}

@media (max-width: 538px) {
  .modal__container {
    padding: 28px 20px 36px 20px;
  }
  .form__select-characters {
    width: calc(100vw - 135px);
    min-width: 185px;
    text-wrap: nowrap;
  }
}
